:root {
  --main-font: 'Roboto', sans-serif;
  --accent-color: #ff6b08;
  --text-color-dark: #000000;
  --text-color-light: #ffffff;
  --text-color-warning: #ff001b;
  --text-color-footer: #545454;
  --button-shadow: 0px 8px 43px rgba(255, 107, 1, 0.6);
  --footer-bg-overlay: #f7f7f7;
  --cubic: cubic-bezier(0.4, 0, 0.2, 1);
}
